import {useState} from 'react'
import { Flex, Input, Button, Image } from '@chakra-ui/react'



const SearchBar = props => {
  let [searchValue,setSearchValue] = useState("")

  const search=searchValue=>{
    fetch(`https://pokeapi.co/api/v2/pokemon/${typeof(searchValue)==='string'?searchValue.toLowerCase():searchValue}`)
      .then(res => {
        if (!res.ok) {
          throw Error("Error")
        }
        setSearchValue("")
        return res.json()

      })
      .then(data => {
        props.onChange(data)
      })
      .catch(error=> {
        console.log(error)
      })

  }

  return (
    <>
      <Image src="https://www.freepnglogos.com/uploads/black-pokemon-logo-transparent-27.png" alt="PokeMon" w="200px" m="0"/>
      <Flex>
          <Input variant="filled" placeholder="Search" value={searchValue} onChange={(e)=>setSearchValue(e.target.value)} size="lg" w={{base:"70%",md:"400px"}}/>
          <Button colorScheme="red" size="lg" variant="solid" borderRadius="lg" m="0px 40px 0px 5px" onClick={()=>search(searchValue)}>Search</Button>
          <Button colorScheme="red" size="lg" variant="solid" borderRadius="lg" onClick={()=>search(Math.floor(Math.random()*899))}>Suprise Me</Button>
      </Flex>
    </>
)
}
export default SearchBar
