import { useState } from 'react' ;
import { Flex, Spacer, VStack, Input, Button, Avatar, Heading, Container, Image } from '@chakra-ui/react'
import SearchBar from './components/SearchBar'
import Details from './components/Details'
import Poketeam from './components/Poketeam'
import { createBreakpoints } from "@chakra-ui/theme-tools"

const breakpoints = createBreakpoints({
  sm: "30em",
  md: "48em",
  lg: "62em",
  xl: "80em",
  "2xl": "96em",
})

function fetchError(err){
  console.log(err)
  let resp = new Response(
    JSON.stringify({
      code: 400,
      message: 'Network Error'
    })
  )
}

function App() {
  let started = false;
  let initialValue = {id:0};
  let [poketeam, setPoketeam] = useState([]);
  let [pokemon,setPokemon] = useState(initialValue);
  (async function initApp () {
      if (pokemon.id===0) {
        initialValue = await(await fetch("https://pokeapi.co/api/v2/pokemon/1").catch(fetchError)).json();
        setPokemon(initialValue);
        started = true
      }
  })();
  const isAMember=()=>{
    let found = false;
    for (let i=0; i<poketeam.length;i++){
      if (poketeam[i].id===pokemon.id){
        found = true;
      }
    }
    return found;
  }

  function addtoTeam () {
    console.log(poketeam)
    let found = isAMember();

    if (found){
      setPoketeam(poketeam.filter((poke)=>poke.id!==pokemon.id))
    } else {
      try {
        if (poketeam.length>5) throw "Exceeded number of Pokemon";
        setPoketeam([...poketeam,pokemon]);
      }
      catch (err){
        console.log(err)
      }
    }
  }
  return (
    <>
      <VStack
        backgroundImage="url('https://backgroundcheckall.com/wp-content/uploads/2017/12/pokemon-background-battle-9.png')"
        backgroundPosition="center"
        backgroundRepeat="no-repeat"
        backgroundSize= "100% 100%"
        w={{base: "100%", md: "90%", lg:"80%"}}
        m="auto auto"
      >

        <SearchBar onChange={state => {setPokemon(state)}}/>
        {
          pokemon.id===0?<Heading></Heading>:<Details currState={pokemon} />
        }
        <Flex alignItems="center">
          {
            console.log(poketeam)}{
            console.log(pokemon.id)
          }
          <Image src={pokemon.sprites?.front_default} alt="No image Available" w="300px" p= "0px" />
          <Button colorScheme="red" size="lg" variant="solid" borderRadius="lg" onClick={()=>addtoTeam()}>{!isAMember()?"Add to Team":"Remove from Team"}</Button>
        </Flex>
        <Poketeam team={poketeam} onChange={state=> {setPokemon(state)}}/>
      </VStack>
    </>
  );
}

export default App;
