import { Flex } from '@chakra-ui/react'
import BallSolo from './BallSolo'

const Poketeam = props => {
  return (
    <Flex w={{base:"100%", mg:"800px"}} l={"100px"} alignItems="center">
      {
        props.team.map((p) => (
          <BallSolo pokemon={p} clicked={selectedPokemon=>props.onChange(selectedPokemon)}/>
        ))
      }
    </Flex>
  )
}

export default Poketeam
