import {Flex, Avatar, Heading, VStack, Input} from '@chakra-ui/react'

const Details = props =>(
  <Flex direction={{base:"column",lg:"row"}} alignItems="center">
  <Avatar src="https://vgboxart.com/resources/render/13697_pokeball-prev.png" m={{base: "20px auto", lg:"115px 10px 100px 2px"}}/>
    <Heading size="3xl" m={{base:"20px auto", lg:"100px 300px 100px 0px"}}>
      {props.currState.name.toUpperCase()}
    </Heading>
    <VStack border="1px" borderColor="gray.200" borderRadius="lg" bgColor="#eee">
      <Heading size="mg" align="center">Type(s)</Heading>
      <Input value={props.currState.types.map(t=>t.type.name)} size="15px"/>
      <Heading size="mg" align="center">Stats</Heading>
      <Flex>
        <Heading size="sm" align="center" m="0px 5px 0px 0px">HP:</Heading>
        <Input value={props.currState.stats[0].base_stat} size="3px"/>
      </Flex>
      <Flex>
        <Heading size="sm" align="center" m="0px 5px 0px 0px">Attack:</Heading>
        <Input value={props.currState.stats[1].base_stat} size="3px"/>
      </Flex>
      <Flex>
        <Heading size="sm" align="center" m="0px 5px 0px 0px">Defense:</Heading>
        <Input value={props.currState.stats[2].base_stat} size="3px"/>
      </Flex>
      <Flex>
        <Heading size="sm" align="center" m="0px 5px 0px 0px">SP Atk.:</Heading>
        <Input value={props.currState.stats[3].base_stat} size="3px"/>
      </Flex>
      <Flex>
        <Heading size="sm" align="center" m="0px 5px 0px 0px">SP Def.:</Heading>
        <Input value={props.currState.stats[4].base_stat} size="1px"/>
      </Flex>
      <Flex>
        <Heading size="sm" align="center" m="0px 5px 0px 0px">Speed:</Heading>
        <Input value={props.currState.stats[5].base_stat} size="3px"/>
      </Flex>
    </VStack>
  </Flex>
)

export default Details
