import { useState } from 'react'
import { Center, Image } from '@chakra-ui/react'

const BallSolo = props => {
  let [pokeRevealed,setPokerevealed] = useState (false)
  let toggleReveal = () => setPokerevealed(!pokeRevealed)
  let handleClick = () => {
    props.clicked(props.pokemon);
  }
  return (
          <Center w={{base:"50px",sm:"60px", md:"75px",lg:"100px"}} l={{base:"50px",sm:"60px", md:"75px",lg:"100px"}} border="0px" borderRadius="50%" bgColor="" onClick={()=>handleClick()}>
            <Image src={pokeRevealed?props.pokemon.sprites.front_default:"https://vgboxart.com/resources/render/13697_pokeball-prev.png"} w={{base:"50px",sm:"60px", md:"75px",lg:"100px"}} l={{base:"50px",sm:"60px", md:"75px",lg:"100px"}} onMouseEnter={()=>toggleReveal()} onMouseOut={()=>toggleReveal()}/>
          </Center>
        )
      }

export default BallSolo
